.collection-preview {
  display: flex;
  flex-direction: column;
  margin-bottom: 30px;

  .title {
    font-size: 28px;
    font-weight: bold;
    margin-bottom: 25px;
  }
  .preview {
    display: flex;
    justify-content: space-between;
    @media screen and(max-width:800px) {
      display: grid;
      grid-gap: 15px;
      grid-template-columns: 1fr 1fr;
    }
  }
}
